* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
.Dashboard {
  width: 100%;
  font-family: sans-serif !important;
}
.menu-line {
  height: 0.5px;
  width: 100%;
  border-bottom: 0.7px #777 solid;
  margin: 15px 0;
}
.DashboardMenu {
  width: 260px;
  height: 100vh;
  background-image: url(../assets/menu-admin.jpg);
  background-size: cover;
  background-position: -180px;
  position: fixed;
  box-shadow: 0 16px 38px -12px rgba(0, 0, 0, 0.56),
    0 4px 25px 0 rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
  z-index: 10000;
  text-transform: uppercase;
  transition: width 0.5s;
}
.DashboardMenu_small {
  width: 80px;
  transition: width 0.5s;
}
.menufixed {
  position: fixed;
}
.DashboardMenu-disable {
  visibility: hidden;
}
.db-menu-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(1, 1, 1, 0.85);
  z-index: -10;
}
.db-menu {
  width: 260px;
  height: 100%;
  color: #eee;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  overflow-x: hidden;
}
.db-menu::-webkit-scrollbar {
  width: 0px;
}
.db-menu-logo {
  margin-top: 5px;
  width: 100%;
  min-height: 45px;
  align-items: center;
}
.db-menu-logo-img {
  min-width: 55px;
  width: 50px;
  height: 45px;
  margin-left: 3px;
}
.logo-text-box {
  height: 45px;
  min-height: 45px;
  min-width: 300px;
  padding-top: 20px;
  justify-content: center;
  align-items: space-between;
  background-color: red;
}
.logo-text {
  font-size: 16px;
  font-family: sans-serif;
  font-weight: bold;
  text-align: center;
}
.db-menu-user {
  width: 100%;
  font-size: 14px;
  cursor: pointer;
}
.db-menu-name {
  flex-grow: 1;
  text-transform: capitalize;
  margin-left: 35px;
  white-space: nowrap;
  font-weight: 300;
  font-size: 13px;
}
.db-menu-top-icon {
  width: 31px;
}
.db-menu-top-icon .icon {
  font-size: 20px;
}
.db-menu-top-icon p {
  font-size: 14px;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
}
.db-menu-name-left {
  text-transform: uppercase;
  font-weight: 300;
  font-size: 14px;
  width: 26px;
  height: 40px;
}
.db-menu-user-opt .db-menu-top {
  height: 40px;
}
.db-menu-avt {
  display: flex;
  align-items: center;
  /* background-color: red; */
}
.db-menu-avt img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-left: 3px;
}
.db-menu-user-opt {
  width: 100%;
  font-size: 14px;
  transition: height 0.4s;
  height: 0;
  overflow: hidden;
}
.db-menu-user-opt .db-menu-top:first-child {
  margin-top: 10px;
}
.closeOpt {
  height: 50px;
}
.closeOpt .db-menu-top:first-child {
  margin-top: 10px;
}
.db-menu-listitem {
  width: 100%;
}
.db-menu-item {
  margin-bottom: 10px;
}
.db-menu-item:hover .db_menu_top_parent {
  background-color: rgba(150, 150, 150, 0.4);
  color: #eee;
}
.db-menu-top {
  font-size: 12px;
  justify-content: flex-start;
  padding: 10px;
  cursor: pointer;
  height: 50px;
  border-radius: 5px;
  transition: background-color 0.2s;
  align-items: center;
  margin-top: 10px;
}
.db-menu-top:first-child {
  margin-top: 0;
}
.db-menu-top:last-child {
  margin-bottom: 0;
}
.db-menu-top:hover {
  background-color: rgba(150, 150, 150, 0.4);
  color: #eee;
}
.db_menu_active {
  background-color: #eee;
  color: #000;
  transition: background-color none;
}
.db_menu_active:hover {
  background-color: #eee;
  color: #000;
}
.db_menu_active_blur {
  background-color: rgba(150, 150, 150, 0.4);
  color: #eee;
}
.DashboardBody {
  padding: 20px 30px;
  background-color: #eaeaea;
  flex-grow: 1;
  margin-left: 260px;
  transition: 0.5s;
  min-height: 100vh;
}
.DashboardBody_small {
  margin-left: 80px;
}
.dashboard-header {
  height: 41px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 38px;
}
.menu-opt {
  cursor: pointer;
  background-color: #fff;
  margin-right: 20px;
  width: 41px;
  height: 41px;
  border-radius: 50%;
  color: #aaa;
  border-color: #999;
  transition: box-shadow 0.3s;
  box-shadow: 0 2px 2px 0 hsla(0, 0%, 60%, 0.14),
    0 3px 1px -2px hsla(0, 0%, 60%, 0.2), 0 1px 5px 0 hsla(0, 0%, 60%, 0.12);
}
.menu-opt:hover {
  box-shadow: 0 14px 26px -12px hsla(0, 0%, 60%, 0.42),
    0 4px 23px 0 rgba(0, 0, 0, 0.12), 0 8px 10px -5px hsla(0, 0%, 60%, 0.2);
}
