}
.dashboard-header p {
  font-size: 18px;
  font-weight: 300;
  font-family: sans-serif;
  color: #777;
}
.menu-notice {
  color: #777;
  margin-left: 40px;
  cursor: pointer;
  position: relative;
}
.menu-notice:hover .icon {
  color: #111;
}
.notice-box {
  width: 250px;
  background-color: #fff;
  border: 1px #111 solid;
  border-radius: 5px;
  position: absolute;
  top: 30px;
  right: 0;
  z-index: 1;
  padding: 0 10px;
  max-height: 375px;
  overflow-y: scroll;
}

/* width */
.notice-box::-webkit-scrollbar {
  display: none !important;
}
/* Track */
.notice-box::-webkit-scrollbar-track {
  display: none !important;
}
/* Handle */
.notice-box::-webkit-scrollbar-thumb {
  display: none !important;
}
.notice-item {
  color: #666;
  font-size: 12px;
  padding: 5px 0;
  border-bottom: 1px #ddd solid;
}
.notice-item:last-child {
  border-bottom: none;
}
.notice-count {
  position: absolute;
  color: #fff;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  font-size: 12px;
  text-align: center;
  line-height: 15px;
  top: -5px;
  right: -10px;
  background-color: red;
}

.menu-search div {
  background: #fff;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: #777;
  box-shadow: 0 2px 2px 0 hsla(0, 0%, 60%, 0.14),
    0 3px 1px -2px hsla(0, 0%, 60%, 0.2), 0 1px 5px 0 hsla(0, 0%, 60%, 0.12);
}
.menu-search div:hover {
  box-shadow: 0 14px 26px -12px hsla(0, 0%, 60%, 0.42),
    0 4px 23px 0 rgba(0, 0, 0, 0.12), 0 8px 10px -5px hsla(0, 0%, 60%, 0.2);
}
.menu-search input {
  font-size: 16px;
  font-weight: 300;
  font-family: sans-serif;
  background: none;
  outline: none;
  border: none;
  height: 40px;
  border-bottom: 2px solid #ddd;
}
.menu-search input:hover {
  border-bottom: 2px solid #777;
}
.menu-search input:focus {
  border-bottom: 2px solid #777;
}

.dashboard-main {
}
.top-location {
  position: relative;
  padding-top: 20px;
  width: 100%;
  height: 389px;
  margin: 40px 0;
}
.chart {
  position: relative;
  padding-top: 20px;
  width: 65%;
}
.chartline {
  width: 99%;
}
.chart .top-location-container,
.chartpie .top-location-container {
  padding-bottom: 10px;
}
.chart .count-line,
.chartpie .count-line {
  margin-top: 10px;
}
.chart .count-up,
.chartpie .count-up {
  color: #777 !important;
}
.chartpie {
  position: relative;
  padding-top: 20px;
  width: 35%;
  margin-right: 30px;
}
.pie canvas {
  width: 100% !important;
  height: 100% !important;
  max-height: 300px !important;
}
.top-location-container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  height: 100%;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 2px 0 hsla(0, 0%, 60%, 0.14),
    0 3px 1px -2px hsla(0, 0%, 60%, 0.2), 0 1px 5px 0 hsla(0, 0%, 60%, 0.12);
}
.headerbox-header {
  width: 100%;
  height: 50px;
  padding-left: 100px;
}
.headerbox-header p {
  font-size: 18px;
  font-family: sans-serif;
  font-weight: 300;
  color: #555;
}
.headerbox {
  color: #fff;
  font-size: 20px;
  width: 65px;
  height: 65px;
  background: linear-gradient(60deg, #66bb6a, #43a047);
  border-radius: 5px;
  position: absolute;
  top: 0;
  left: 20px;
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.14),
    0 7px 10px -5px rgba(76, 175, 80, 0.4);
}
.top-location-content {
  flex-grow: 1;
}
.top-location-list {
  width: 50%;
}
.top-location-div {
  width: 100%;
  border-bottom: 0.5px #ddd solid;
  padding: 15px 10px;
  justify-content: space-between;
  cursor: pointer;
  transition: 0.3s;
}
.top-location-div:hover {
  background: #eee;
}
.header div {
  color: #000 !important;
  font-weight: 500;
}
.top-location-div div {
  font-family: sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #777;
}
.top-location-div:last-child {
  border: none;
}
.top-location-div:first-child:hover {
  background: none;
}
.top-location-map {
  width: 50%;
  flex-grow: 1;
  margin-left: 30px;
}

.row {
  width: 100%;
  justify-content: space-between;
  margin-bottom: 40px;
}
.total-count-item:last-child {
  margin: 0;
}
.total-count-item {
  padding-top: 20px;
  position: relative;
  margin-right: 30px;
  width: 50%;
}
.total-count-item-container {
  width: 100%;
  background: #fff;
  box-shadow: 0 2px 2px 0 hsla(0, 0%, 60%, 0.14),
    0 3px 1px -2px hsla(0, 0%, 60%, 0.2), 0 1px 5px 0 hsla(0, 0%, 60%, 0.12);
  border-radius: 5px;
  padding: 10px;
}
.count {
  width: 65px;
  height: 65px;
}
.orange {
  background: linear-gradient(60deg, #ffa726, #fb8c00);
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.14),
    0 7px 10px -5px rgba(255, 152, 0, 0.4);
}
.pink {
  background: linear-gradient(60deg, #ec407a, #d81b60);
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.14),
    0 7px 10px -5px rgba(233, 30, 99, 0.4);
}
.lightblue {
  background: linear-gradient(60deg, #26c6da, #00acc1);
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.14),
    0 7px 10px -5px rgba(0, 188, 212, 0.4);
}
.dashboard-count p {
  height: 40px;
  font-size: 26px;
  font-family: sans-serif;
  font-weight: 500;
  width: 100%;
  text-align: center;
  line-height: 40px;
  color: #666;
  margin-top: 20px;
  margin-bottom: 10px;
}
.total-count-header {
  margin-left: 80px;
}
.total-count-header p {
  font-size: 16px;
  font-family: sans-serif;
  font-weight: 300;
  color: #777;
  margin-top: 10px;
}
.count-line {
  width: 100%;
  height: 1px;
  background-color: #ddd;
}
.count-status {
  font-size: 14px;
  margin-top: 10px;
}
.count-status p {
  font-family: sans-serif;
  font-weight: 300;
  color: #666;
}
.count-up {
  color: green !important;
  margin-right: 5px;
}
.count-down {
  color: red !important;
  margin-right: 5px;
}

.topfive:last-child {
  margin: 0;
}
.topfive {
  padding-top: 20px;
  position: relative;
  margin-right: 30px;
  width: 50%;
  margin-bottom: 40px;
}
.topfive-container {
  width: 100%;
  background: #fff;
  box-shadow: 0 2px 2px 0 hsla(0, 0%, 60%, 0.14),
    0 3px 1px -2px hsla(0, 0%, 60%, 0.2), 0 1px 5px 0 hsla(0, 0%, 60%, 0.12);
  border-radius: 5px;
  padding: 10px;
}
.topfive-content {
  flex-grow: 1;
}
.topfive-list {
  /* padding-top: 10px; */
  width: 100%;
}
/* width */
.todo-list::-webkit-scrollbar {
  display: none !important;
}
/* Track */
.todo-list::-webkit-scrollbar-track {
  display: none !important;
}
/* Handle */
.todo-list::-webkit-scrollbar-thumb {
  display: none !important;
}
.top-user-name {
  font-family: sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #777;
}
.top-user {
  align-items: center;
}
.top-user-avt {
  width: 40px;
  height: 40px;
  margin-right: 20px;
  border-radius: 50%;
}
.topfive-div {
  align-items: center;
  transition: 0.5s;
}
.topfive-header:last-child {
  width: 80px;
}

.topfive-div {
  width: 100%;
  border-bottom: 0.5px #ddd solid;
  padding: 10px 10px;
  justify-content: space-between;
  cursor: pointer;
  transition: 0.3s;
}
.topfive-div:hover {
  background: #eee;
}
.topfive-div div {
  font-family: sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #777;
}
.topfive-div:last-child {
  border: none;
}
.recent-padding {
  padding-top: 10px !important;
  padding-bottom: 10px !important;
}
.review-product {
  flex-grow: 1;
  width: 60%;
  margin: 0 10px;
  color: #111;
  font-family: sans-serif !important;
}
.review-product-star {
  width: 80px;
  min-width: 80px;
}
.star-rating {
}
.star-rating .star {
  margin: 0 2px;
  font-size: 12px;
  color: #ddd;
}
.star-rating-list {
  position: relative;
  overflow: hidden;
}
.star-color {
  color: #fda32a !important;
}
.review-product-item {
  align-items: flex-end;
  justify-content: space-between;
}
.review-product-item:first-child {
  margin-bottom: 7px;
}
.review-product-name p {
  margin-right: 26px;
  margin-left: 10px;
  font-size: 14px;
  font-family: sans-serif !important;
}
.review-product-name h5 {
  font-size: 14px;
  font-family: sans-serif !important;
  color: #333;
  /* Add ... if text is full */
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-right: 5px;
}
.review-product-time p {
  font-size: 12px;
  font-family: sans-serif !important;
  color: #777;
}
.review-product-item-content {
  color: #777;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  max-width: 100%;
  overflow: hidden;
  height: 20px;
}
.bottom p {
  height: 18px;
  font-size: 14px;
  font-weight: 500;
  font-family: sans-serif;
}

.boxchat-admin {
  background-color: #fff;
  width: 100%;
  height: calc(100vh - 119px);
  border-radius: 5px;
  padding: 20px;
  overflow: hidden;
  position: relative;
}
.boxchat-left {
  width: 270px;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.boxchat-list {
  flex-grow: 1;
  width: 100%;
  padding-right: 20px;
  overflow-y: scroll;
}
/* width */
.boxchat-list::-webkit-scrollbar {
  display: none !important;
}
/* Track */
.boxchat-list::-webkit-scrollbar-track {
  display: none !important;
}
/* Handle */
.boxchat-list::-webkit-scrollbar-thumb {
  display: none !important;
}

.boxchat-item {
  margin: 10px 0;
  height: 64px;
  border-radius: 10px;
  padding: 10px;
}
.boxchat-item:last-child {
  margin-bottom: 0;
}

.boxchat-avt {
  margin-right: 10px;
}
.boxchat-avt img {
  border-radius: 50%;
  width: 48px;
  height: 48px;
}
.boxchat-name {
  font-size: 16px;
  text-transform: capitalize;
  font-family: sans-serif;
}

.boxchat-first {
  width: 100%;
}
.boxchat-first p {
  font-size: 14px;
  color: #777;
  font-family: sans-serif;
  margin-bottom: 2px;
}
.boxchat-first p:first-child {
  /* Add ... if text is full */
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  max-width: 120px;
  overflow: hidden;
  margin-right: 5px;
}
.boxchat-first p:last-child {
  max-width: 50px;
  text-align: right;
}
.boxchat-item:hover {
  background-color: #eee;
}
.boxchat-item-active {
  background-color: #eee;
}
.boxchat-main {
  flex-grow: 1;
  height: 100%;
  border-left: 1px #ddd solid;
}
.boxchat-box {
  width: 100%;
  height: 90%;
  padding-left: 20px;
}
.boxchat-box-info {
  display: flex;
  height: 36px;
  margin-bottom: 10px;
}
.boxchat-box-avt {
  height: 100%;
  margin-bottom: 20px;
  justify-content: flex-start;
  margin-right: 10px;
}
.boxchat-box-avt img {
  border-radius: 50%;
  width: 32px;
  height: 32px;
}
.boxchat-contents {
  border-top: 1px #ddd solid;
}
.boxchat-contents-item {
  margin-top: 10px;
}
.boxchat-search {
  width: 100%;
  padding-right: 20px;
  margin-bottom: 10px;
}
.boxchat-search input {
  width: 100%;
  height: 36px;
  outline: none;
  border: 1px #ddd solid;
  border-radius: 10px;
  padding: 0 10px;
}
.boxchat-type {
  height: 50px;
  width: 100%;
  padding-left: 20px;
}
.boxchat-type-form {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.boxchat-type input {
  outline: none;
  border: 1px #ddd solid;
  padding: 0 10px;
  width: 90%;
  height: 70%;
  border-radius: 10px;
  margin-right: 5px;
}
.boxchat-type button {
  width: 10%;
  height: 70%;
  border: none;
  background-color: #111;
  border-radius: 10px;
  cursor: pointer;
  color: #fff;
}

.box-chat-clienttext {
  margin: 3px 0;
  clear: right;
  position: relative;
  width: max-content;
}
.box-chat-clienttext p {
  font-size: 14px;
  margin: 0;
  border-radius: 15px;
  padding: 5px 8px;
  width: max-content;
  color: #fff;
  background-color: #0e0e0e;
  font-family: sans-serif;
}
.box-chat-admintext {
  margin: 3px 0;
  text-align: right;
  float: right;
  clear: right;
  position: relative;
}
.box-chat-admintext p {
  font-size: 14px;
  border-radius: 15px;
  padding: 5px 8px;
  text-align: right;
  width: max-content;
  background-color: #eeecec;
  font-family: sans-serif;
}
.time-tooltip {
  background-color: #111;
  color: #fff;
  border-radius: 2px;
  padding: 0 20px;
  position: absolute;
  top: 0;
  right: calc(100% + 10px);
  width: max-content;
  height: 100%;
}
.time-tooltip p {
  font-size: 10px;
  background-color: #111;
}
.time-tooltip::after {
  content: ' ';
  position: absolute;
  top: 35%;
  right: -10px;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent black;
}
.time-tooltip-client {
  background-color: #111;
  color: #fff;
  border-radius: 2px;
  padding: 0 20px;
  position: absolute;
  top: 0;
  left: calc(100% + 10px);
  width: max-content;
  height: 100%;
}
.time-tooltip-client p {
